<template>
  <div id="app">
    <header class="header">
      <section class="header__top">
        <div class="header__top__wrapper">
          <img src="img/logo.jpg" alt="logo">
        </div>
      </section>
      <section class="header__main text-center">
        <h1>iTunes api example</h1>

        <div action class="header__main__form flex justify-center">
          <input
            v-model="searchWords"
            @keyup.enter="checkSearchWords"
            class="header__main__form__input"
            type="text"
            name="search"
            placeholder="Search songs.."
          >
          <button @click="checkSearchWords" class="header__main__form__search-button">search</button>
        </div>
        <p
          class="header__main__form__p"
        >Search by song title, author, song number, lyrics, catalog or copyright owner</p>
      </section>
    </header>
    <resultComponent v-if="posts" :posts="posts"></resultComponent>
    <footer class="footer flex align-center justify-center" :class="{footer_absl: footer_absl}">
      <p>Powered by PGS</p>
    </footer>
  </div>
</template>

<script>
import resultComponent from "./components/result.vue";

import axios from "axios";

export default {
  name: "app",
  data() {
    return {
      posts: null,
      searchWords: null
    };
  },
  methods: {
    checkSearchWords() {
      axios
        .get(
          "https://cors-anywhere.herokuapp.com/https://itunes.apple.com/search?term=" +
            this.searchWords +
            "&media=music&limit=1000"
        )
        .then(response => (this.posts = response.data));
    }
  },
  components: { resultComponent },
  computed:{
    footer_absl(){
        if(!this.posts || this.posts.resultCount == 0){
          return true;
        }
        else{
          return false;
        }
    }
  }
};
</script>

