<template>
  <main class="main text-center">
    <div class="main__wrapper">
      <p v-if="posts.resultCount" class="songs-count">Found {{ posts.resultCount }} songs</p>
      <p v-else class="songs-count">Sorry, no matches found</p>
      <div v-if="posts" class="row flex flex-wrap justify-space">
        <section
          v-for="(post, id) in postsToShow"
          :key="post+id"
          class="main__elem col-xl-4 col-lg-12"
        >
          <div class="main__elem__pic-wrapper">
            <transition name="fade" mode="out-in">
              <img :src="post.artworkUrl100" alt="pic" :key="post.artworkUrl100">
            </transition>
          </div>
          <div class="main__elem__txt-wrapper">
            <transition name="fade" mode="out-in">
              <p class="main__elem__txt-wrapper__heading" :key="post.trackName">{{ post.trackName }}</p>
            </transition>
            <transition name="fade" mode="out-in">
              <p
                class="main__elem__txt-wrapper__author"
                :key="post.artistName"
              >{{ post.artistName }}</p>
            </transition>
          </div>
        </section>
      </div>
      <section v-if="posts.resultCount" class="control-buttons flex justify-center">
        <button
          class="control-buttons__button prev flex align-center justify-center"
          v-if="page>1"
          @click="page--"
        >&#8249;&#8249; prev</button>
        <button
          class="control-buttons__button next flex align-center justify-center"
          v-if="page*postsOnPage<posts.resultCount"
          @click="page++"
        >next &#8250;&#8250;</button>
      </section>
    </div>
  </main>
</template>

<script>
export default {
  name: "Result",
  props: ["posts"],
  data() {
    return {
      postsOnPage: 9,
      page: 1
    };
  },
  computed: {
    resetPage() {
      if (this.postsOnPage * this.page > this.posts.results.length) {
        return this.page = 1;
      }
    },
    postsToShow: function() {
      let postsOnPage = this.postsOnPage;
      let page = this.page;
      return this.posts.results.filter(function(post, index) {
        if (
          index < postsOnPage * page &&
          index > postsOnPage * page - postsOnPage - 1
        ) {
          return post;
        }
      });
    }
  }
};
</script>

